.video-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr)); 
  gap: 20px; 
  justify-items: center; 
}

.video-container {
    margin: 20px;
    padding: 10px;
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: transform 0.2s;
  }
  
  .video-container:hover {
    transform: scale(1.05);
  }
  
  .video-title {
    margin-top: 10px;
    font-size: 1em;
    text-align: center;
    color: white;
  }
  
  .video-link {
    text-decoration: none;
    color: inherit;
  }
  